<template>
  <el-row type="flex" class="row-bg" align="middle" justify="center">
    <el-col :xs="20" :sm="14" :md="14" :lg="11" :xl="11">
      <el-form
        ref="loginForm"
        :model="form"
        label-position="top"
        label-width="80px"
        class="login-form"
        :rules="rules"
      >
        <el-form-item label="账号" prop="username">
          <el-input v-model="form.username" placeholder="请输入帐号"></el-input>
        </el-form-item>

        <el-form-item v-if="visible" label="密码" prop="password">
          <el-input v-model="form.password" placeholder="请输入密码" show-password></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="SubmitForm('loginForm')">登入</el-button>
          <el-button @click="resetForm('loginForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>


<script>
import axios from "axios";

export default {
  data() {
    return {
      form: {
        username: "",
        password: ""
      },
      visible: true,
      rules: {
        username: [
          { required: true, message: "请输入账号", trigger: "blur" },
          { min: 3, max: 12, message: "长度在 3 到 12 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    SubmitForm(loginForm) {
      this.$refs[loginForm].validate(value => {
        if (value) {
          axios({
            url: "http://localhost:8888/api/private/v1/login",
            method: "post",
            data: this.form
          }).then(({ data: { data, meta } }) => {
            if (meta.status == 200) {
              localStorage.setItem("token", data.token);
              this.$router.push("./home");
            } else {
              alert("账户或密码错误");
              return false;
            }
          });
        }
      });
    },

    resetForm(loginForm) {
      this.$refs[loginForm].resetFields();
    }
  }
};
</script>


<style>
.row-bg {
  height: 100%;
  width: 100%;
  background: url(../assets/74956778_p0_master1200.jpg) no-repeat center;
}
.login-form {
  background-color: rgba(255, 255, 255, 0.6);
  /* opacity: 0.6; */
  border-radius: 10px;
  padding: 30px 20px;
  min-width: 400px;
}
</style>

